import type { EventEmitter } from '@stencil/core';
import { Component, Host, h, Prop, Event, Element } from '@stencil/core';

@Component({
	tag: 'o-step',
	styleUrl: 'o-step.scss',
	shadow: true
})
export class OStep {
	@Element() hostElement!: HTMLOStepElement;

	/** Visual variant of the step */
	@Prop() variant: 'completed' | 'active' | 'default' | 'error' = 'default';

	/** Indicates whether the step is currently selected */
	@Prop() selected: boolean = false;

	/** Enables click interaction for the step */
	@Prop() clickable: boolean = false;

	/** Position of the step within the stepper (internal) */
	@Prop() position: 'first' | 'last' | 'single' | 'inner' = 'inner';

	/** (Circle Number) Index of the step (starting from 1) */
	@Prop() index: number = 1;

	/** Emitted when the step is clicked and its selection state changes */
	@Event() oSelectedChanged: EventEmitter<HTMLOStepElement>;

	private get baseIconClasses() {
		return 'w-[38px] h-[38px] flex items-center justify-center rounded-full text-base font-medium font-inter m-1';
	}

	private handleClick() {
		if (this.clickable) {
			this.oSelectedChanged.emit(this.hostElement);
		}
	}

	render() {
		// Label Class
		let labelClass = 'text-tab_grey';
		switch (this.variant) {
			case 'error':
				labelClass = 'text-error_red';
				break;
			case 'completed':
				labelClass = 'text-main_text';
				break;
			case 'active':
				labelClass = 'text-oec_blue';
				break;
		}

		// Icon Element
		let iconElement;
		switch (this.variant) {
			case 'error':
				iconElement = (
					<div class={`${this.baseIconClasses} border-2 border-error_red bg-white text-error_red`}>
						<o-icon name="triangle-exclamation" color="error_red" size="sm" class="scale-[1.4]" />
					</div>
				);
				break;
			case 'completed':
				iconElement = (
					<div class={`${this.baseIconClasses} border-2 border-green_txt bg-green_txt text-white`}>
						<o-icon name="check" color="white" size="sm" class="scale-150" />
					</div>
				);
				break;
			case 'active':
				iconElement = (
					<div class="rounded-full border-2 border-oec_blue inline-block m-0.5 bg-white">
						<div class="size-[34px] flex items-center justify-center rounded-full border border-white bg-oec_blue text-white text-base font-medium font-inter m-[0.12rem]">
							{this.index}
						</div>
					</div>
				);
				break;
			default:
				iconElement = (
					<div class={`${this.baseIconClasses} border-2 border-tab_grey bg-white text-tab_grey`}>{this.index}</div>
				);
		}

		return (
			<Host
				tabIndex={this.clickable ? 0 : -1}
				onClick={() => this.handleClick()}
				class={{
					step: true,
					'step-selected': this.selected,
					'step-clickable': this.clickable,
					[`step-position-${this.position}`]: true,
					[`step-variant-${this.variant}`]: true
				}}
			>
				<div class="flex flex-row items-start space-x-4 space-y-3">
					{/* Icon & connector wrapper */}
					<div class="relative flex flex-col items-center min-h-[80px]">
						{' '}
						{/* adjust min-h for spacing */}
						{/* Top line */}
						{(this.position === 'inner' || this.position === 'last') && (
							<div class="absolute top-0 h-1/2 w-[2px] bg-tab_grey z-0" />
						)}
						{/* Circle */}
						<div class="z-10">{iconElement}</div>
						{/* Bottom line */}
						{(this.position === 'first' || this.position === 'inner') && (
							<div class="absolute bottom-0 h-1/2 w-[2px] bg-tab_grey z-0" />
						)}
					</div>

					{/* Label */}
					<div class={`text-sm font-inter ${labelClass}`}>
						<slot></slot>
					</div>
				</div>
			</Host>
		);
	}
}
